document.addEventListener('DOMContentLoaded', () => {
    const phoneNumberInput = document.getElementById('phoneNumber');
    const callButton = document.getElementById('callButton');
    const iframe = document.getElementById('zoom-embeddable-phone-iframe');

    callButton.addEventListener('click', () => {
        const phoneNumber = phoneNumberInput.value;
        if (phoneNumber) {
            iframe.contentWindow.postMessage({
                type: 'zp-make-call',
                data: {
                    number: phoneNumber,
                    autoDial: true
                }
            }, 'https://applications.zoom.us');
        } else {
            alert('ÈõªË©±Áï™Âè∑„ÇíÂÖ•Âäõ„Åó„Å¶„Åè„Å†„Åï„ÅÑ„ÄÇ');
        }
    });

    // Zoom Phone Smart Embed„ÅÆÂàùÊúüÂåñ
    iframe.contentWindow.postMessage({
        type: 'zp-init-config',
        data: {
            enableSavingLog: false,
            enableAutoLog: false,
            enableContactSearching: false,
            enableContactMatching: false
        }
    }, 'https://applications.zoom.us');
});